.server-banner {
    max-width: 640px;
    margin: 3rem auto;

    header {
        height: 110px;
        background-size:cover;
    }
    header > .titles {
        height: 100%;
        width: 100%;
        display: flex;
        align-items: center;
        padding: 1rem;
        background: linear-gradient(to bottom, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0.4) 50%, rgba(0,0,0,0.2) 100%);
    }

    header > .titles .game-logo {
        margin-left: 1rem;
        font-size: 0;
    }

    header > .titles .server-name {
        margin: 0 1.5rem;
        font-size: 1.8rem;
        font-weight: 400;
        color: $light-color;
    }
}
